import React from 'react';

class Pokemon extends React.Component {
  render() {
    const { name, type, image, averageWeight, taille } = this.props.pokemon;
    return (
      <div className="container">
        <div className="text">
          <p>{name}</p>
          <p>{type}</p>
          <p>{averageWeight.value} kg</p>
          <p>
            Taille : {taille.valeur} {taille.unite}
          </p>
        </div>
        <img src={image} height="100px" alt="Logo" />
      </div>
    );
  }
}

export default Pokemon;
